	body.is-mobile #wrapper:before {
		background-attachment: scroll;
		background-position: 50% -3%;
		background-repeat: repeat-y;
		background-size: 150% auto;
	}

	body.is-mobile #wrapper > section > header {
		background-attachment: scroll;
		background-position: 25% 50%;
	}

	body.is-mobile #wrapper > section.intro > header {
		background-position: 25% 23%;
	}

	#wrapper:before {
		-webkit-transition: opacity 1s ease;
		transition: opacity 1s ease;
	}

		body.is-preload #wrapper:before {
			opacity: 0;
		}

	#wrapper > section.intro {
		-webkit-transition: opacity 1s ease;
		transition: opacity 1s ease;
		opacity: 1;
	}

		#wrapper > section.intro > header {
			-webkit-transition: -webkit-transform 1s ease;
			transition: -webkit-transform 1s ease;
			transition: transform 1s ease;
			transition: transform 1s ease, -webkit-transform 1s ease;
		}

		#wrapper > section.intro > .content {
			-webkit-transition: -webkit-transform 1s ease;
			transition: -webkit-transform 1s ease;
			transition: transform 1s ease;
			transition: transform 1s ease, -webkit-transform 1s ease;
		}

		body.is-preload #wrapper > section.intro {
			opacity: 0;
		}

			body.is-preload #wrapper > section.intro > header {
				-webkit-transform: translateY(1rem);
				        transform: translateY(1rem);
			}

			body.is-preload #wrapper > section.intro > .content {
				-webkit-transform: translateY(-1rem);
				        transform: translateY(-1rem);
			}

		@media screen and (max-width: 1280px) {

			#wrapper > section.intro > header > * {
				-webkit-transition: opacity 1s ease, -webkit-transform 1s ease;
				transition: opacity 1s ease, -webkit-transform 1s ease;
				transition: transform 1s ease, opacity 1s ease;
				transition: transform 1s ease, opacity 1s ease, -webkit-transform 1s ease;
			}

			body.is-preload #wrapper > section.intro > header {
				-webkit-transform: none;
				        transform: none;
			}

				body.is-preload #wrapper > section.intro > header > * {
					-webkit-transform: translate(-0.5rem);
					        transform: translate(-0.5rem);
					opacity: 0;
				}

			body.is-preload #wrapper > section.intro > .content {
				-webkit-transform: none;
				        transform: none;
			}

		}

	body.is-ie #wrapper section {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
	}

		body.is-ie #wrapper section > header {
			width: 50%;
		}

		body.is-ie #wrapper section > .content {
			width: 50%;
		}

		body.is-ie #wrapper section > footer {
			width: 50%;
			text-align: left;
			margin-left: 50%;
			padding: 5rem;
		}

	body.is-ie #wrapper > section > .content > section {
		width: 100vw;
	}

	@media screen and (max-width: 1280px) {

		body.is-ie #wrapper section > footer {
			padding: 4rem;
		}

	}

body {
    overflow-y: hidden;
    overflow-x: hidden;
    margin: 0;
    padding: 0;
}

.logo_picture {
    height: auto;
    width: auto;
    max-width: 100px;
    max-height: 100px;
}

.navigation_bar {
    list-style: none;
    position:fixed;
    top:0;
    margin: auto;
}

.survey_frame {
    top: 10%;
    height: 90%;
    width: 100%;
}

.survey_progress {
    position:fixed;
    bottom:0;
    background-color: #efefef;
    width: 100%;
    height: auto;
    margin: auto;
}

.prog {
    margin: 2%;
}

.question_icon {
    display: block;
    height: auto;
    width: auto;
    margin-left: auto;
    margin-right: auto;
    max-width: 150px;
    max-height: 150px;
}

.survey_question {
    margin: auto;
    color: black;
    font-family: 'Roboto', sans-serif;
    font-size: 200%;
    font-weight: lighter;
    padding: 0;
    text-align: center;
}

.option_list {
    padding: 0;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 62%;
    overflow: hidden;
}

.option_list li {
    float: left;
    list-style: none;
    text-align: center;
    margin-right: 40px;
    width: 150px;
    line-height: 45px;
    background-color: #efefef;
    border-radius: 10px;
}

.survey_button {
    color: dimgray;
    font-family: 'Roboto', sans-serif;
    border: none;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}

.survey_button {
  text-decoration: none;
  color: dimgray;
  display: block;
}

.survey_button:hover {
  text-decoration: none;
  color: black;
  background-color: #43d9b8;
  border-radius: 10px;
}

